"use client"

import { useState } from "react"
import { Button } from "@/components/ui/button"
import { Input } from "@/components/ui/input"
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table"
import { Dialog, DialogContent, DialogHeader, DialogTitle, DialogTrigger } from "@/components/ui/dialog"
import { Label } from "@/components/ui/label"
import { Select, SelectContent, SelectItem, SelectTrigger, SelectValue } from "@/components/ui/select"

interface Product {
  id: string
  name: string
  price: number
  stock: number
}

interface Sale {
  id: string
  productId: string
  productName: string
  quantity: number
  price: number
  total: number
  date: string
}

interface SalesManagementProps {
  products: Product[]
  onAddSale: (sale: Sale) => void
}

export function SalesManagement({ products, onAddSale }: SalesManagementProps) {
  const [newSale, setNewSale] = useState({ productId: "", quantity: "" })
  const [isDialogOpen, setIsDialogOpen] = useState(false)

  const addSale = () => {
    const product = products.find(p => p.id === newSale.productId)
    if (product && newSale.quantity && Number(newSale.quantity) > 0) {
      const quantity = Number(newSale.quantity)
      if (quantity <= product.stock) {
        const sale: Sale = {
          id: Date.now().toString(),
          productId: product.id,
          productName: product.name,
          quantity: quantity,
          price: product.price,
          total: product.price * quantity,
          date: new Date().toLocaleDateString()
        }
        
        onAddSale(sale)
        setNewSale({ productId: "", quantity: "" })
        setIsDialogOpen(false)
      }
    }
  }

  return (
    <div className="space-y-4">
      <div className="flex justify-between items-center">
        <h2 className="text-2xl font-bold">Gerenciamento de Vendas</h2>
        <Dialog open={isDialogOpen} onOpenChange={setIsDialogOpen}>
          <DialogTrigger asChild>
            <Button>Nova Venda</Button>
          </DialogTrigger>
          <DialogContent>
            <DialogHeader>
              <DialogTitle>Registrar Venda</DialogTitle>
            </DialogHeader>
            <div className="space-y-4">
              <div className="space-y-2">
                <Label htmlFor="product">Produto</Label>
                <Select
                  value={newSale.productId}
                  onValueChange={(value) => setNewSale({ ...newSale, productId: value })}
                >
                  <SelectTrigger>
                    <SelectValue placeholder="Selecione um produto" />
                  </SelectTrigger>
                  <SelectContent>
                    {products.map((product) => (
                      <SelectItem key={product.id} value={product.id}>
                        {product.name} (Estoque: {product.stock})
                      </SelectItem>
                    ))}
                  </SelectContent>
                </Select>
              </div>
              <div className="space-y-2">
                <Label htmlFor="quantity">Quantidade</Label>
                <Input
                  id="quantity"
                  type="number"
                  value={newSale.quantity}
                  onChange={(e) => setNewSale({ ...newSale, quantity: e.target.value })}
                  placeholder="0"
                />
              </div>
              <Button onClick={addSale} className="w-full">
                Registrar Venda
              </Button>
            </div>
          </DialogContent>
        </Dialog>
      </div>

      <div className="rounded-md border">
        <Table>
          <TableHeader>
            <TableRow>
              <TableHead>Data</TableHead>
              <TableHead>Produto</TableHead>
              <TableHead>Quantidade</TableHead>
              <TableHead>Preço Unit.</TableHead>
              <TableHead>Total</TableHead>
            </TableRow>
          </TableHeader>
          <TableBody>
            {/* Sales will be managed by the parent component */}
          </TableBody>
        </Table>
      </div>
    </div>
  )
}
